const DRAWER = 'DRAWER'

function drawerCha (value) {
    return {
        type: DRAWER,
        value
    }
}
function test () {
    return setTimeout(() => {
        return 2+2
    }, 500)
}
export function drawerChange (value) {
    return {
        type: DRAWER,
        value
    }
}

export default function standard (state=null, action) {
    switch(action.type) {
        case DRAWER :
            console.log(state, action)
            return state
        default :
            return state
    }
}