import React from 'react';
import './sidebar.css';
import {drawerChange} from '../reducers/index';
import { connect } from 'react-redux';
import ReactDOM from 'react-dom';

class SideBar extends React.Component {
    //STATE AND OPEN DRAWER NEED TO EXIST ON APP THEN ATTACH LISTENER TO ROOT
    //IF DRAWER IS OPEN AND ROOT IS CLICKED THEN CLOSE THE DRAWER
    //ORRR... 
    //DO SOMETHING WITH E.TARGET
    //WHILE DRAWER IS OPEN IF E.target IS NOT THE DRAWER THEN CLOSE THE DRAWER
    //HAVE SIDEBAR TAKE IN PROP OF THE PARENT ELEMENT (WINDOW) OF WHERE SIDEBAR IS LOCATED, 
    //IF PARENT ELEMENT IS CLICKED WHILE SIDEBAR OPEN THEN IT CLOSES
    state = {
        drawerOpen: false
    }
    openDrawer = () => {
        this.setState((prev) => ({
            drawerOpen: true,
        }))
    }
    closeDrawer = () => {
        console.log('closed')
        this.setState((prev) => ({
            drawerOpen: false,
        }))
    }
    componentDidMount() {
        const {parent} = this.props;
        const parentEl = parent !== null ? this.props.parent : null;
        setTimeout(() => {
            console.log(parentEl.current, parentEl)
            const tryThis = ReactDOM.findDOMNode(parentEl.current)
            console.log('try', tryThis)
            parentEl.current.addEventListener('click', this.closeDrawer)
        }, 1000)
        let obj = {'test': true}
        this.props.dispatch(drawerChange(obj))
    }
    render() {
        const {drawerOpen} = this.state;
        return (
            <React.Fragment>
            {!drawerOpen &&
            <div
            onClick={this.openDrawer}
            >
              Sidebar
            </div>
            }
            {drawerOpen &&
            <div
            onClick={this.openDrawer}
            className="list"
            >
                <ul>
                    <li>Dashboard</li>
                    <li>View Schedule</li>
                    <li>Upload</li>
                </ul>
                <button
                className="logout"
                >Logout</button>
              
            </div>
            }
            </React.Fragment>
        )
    }
}

export default connect((state) => ({

}))(SideBar);